  // Function to get all officers in a flat list
  export const getAllOfficers = () => {
    let allOfficers = [];
    
    for (const rank in officerData) {
      allOfficers = allOfficers.concat(officerData[rank]);
    }
    
    return allOfficers;
  };
  
  // Function to get officers by rank
  export const getOfficersByRank = (rank) => {
    return officerData[rank] || [];
  };
  
  export default officerData;